/**
 * HeaderDescWithPrice.js
 * Product summary with price
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import React, { Component } from 'react';
import { Row, Col } from 'reactstrap';

import PropTypes from 'prop-types';

import './HeaderDescWithPrice.scss';

class HeaderDescWithPrice extends Component {
  render() {
    const {
      logo,
      logoName,
      insurerName,
      insurerDesc,
      cost,
      year,
    } = this.props;

    const isRenderPrice = cost && year;

    return (
      <Row className="header-desc-with-price-container" noGutters>
        <Col className="insurer-logo-container">
          <div className="company-logo-container">
            <img className="company-logo" src={logo} alt={logoName} />
          </div>
        </Col>
        <Col className="insurer-desc-container">
          <div className="insurer-desc-top">
            <span className="insurer-name">{insurerName}</span>
            <div className="resp-price">
              <span className="cost">{cost}</span>
              <span className="unit">{year}</span>
            </div>
          </div>
          <p className="desc">
            {insurerDesc}
          </p>
        </Col>
        {
          isRenderPrice ?
            <Col className="price-container align-self-center">
              <p>
                <span className="cost">{cost}</span>
                <span className="unit">{year}</span>
              </p>
            </Col>
          :
            null
        }
      </Row>
    );
  }
}

HeaderDescWithPrice.defaultProps = {
  cost: null,
  year: null,
};

HeaderDescWithPrice.propTypes = {
  logo: PropTypes.string.isRequired,
  logoName: PropTypes.string.isRequired,
  insurerName: PropTypes.string.isRequired,
  insurerDesc: PropTypes.string.isRequired,
  cost: PropTypes.string,
  year: PropTypes.string,
};

export default HeaderDescWithPrice;
