/**
 * MotorChoosePlanContainer.js
 * Choose Plan container for motor
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import window from 'global/window';
import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { getFormValues } from 'redux-form';

import PropTypes from 'prop-types';
import MotorChoosePlan from '../../components/Motor/MotorChoosePlan';
import RedirectPopup from '../../components/Templates/RedirectPopup/RedirectPopup';

import { setSelectedPlanData, postToFWD } from '../../actions/motorActions';
import { setInsuranceFormState, parseData, FORMSTATES } from '../../actions/insuranceActions';
import { setPlanType } from '../../actions/compareActions';
import { setLoading, clearLoading } from '../../actions/loadingScreenActions';
import { setRestrictionPopup } from '../../actions/restrictionsActions';
import { fbTag, gaTag, ampTag } from '../../lib/tagFunctions';

class MotorChoosePlanContainer extends Component {
  constructor(props) {
    super(props);

    const {
      setFormState,
      loadingEnd,
      plansData,
      selectedIndex,
    } = this.props;

    let isFWD = false;

    try {
      isFWD = plansData[selectedIndex].info.insurer_slug === 'fwd';
    } catch (error) {
      console.log('Unable to get FWD');
    }

    const parsedData = parseData(plansData, null, null, null, (data, category) => {
      if (isFWD) {
        this.RedirectPop.openRedirectPopup(category);
        ampTag('MotorRedirectS');
        return;
      }

      setFormState(FORMSTATES.DETAILFORM.index);
    }, this.props.setSelectedPlanData, loadingEnd, this.props.setRestrictionPopup, selectedIndex);

    this.state = {
      parsedData,
      isFWD,
    };

    this.fwdCheckout = this.fwdCheckout.bind(this);
    this.othersSetSelection = this.othersSetSelection.bind(this);
  }

  componentDidMount() {
    const { setFormState } = this.props;
    setFormState(FORMSTATES.CHOOSEPLAN.index);

    window.scrollTo(0, 0);
    fbTag('PPS2 - Pageview - Motor - ChoosePlan', { Product: 'Motor' });
    gaTag({
      page_title: 'Motor - Choose Plan',
      page_path: '/motor/chooseplan',
    });
    ampTag('MotorPlanS');
  }

  othersSetSelection(clientSlug, clientName, policyName) {
    this.props.setSelectedPlanData({ clientSlug, clientName, policyName });
  }

  fwdCheckout(popupData) {
    const { motorForm, motorMoreForm } = this.props;
    postToFWD(popupData, motorForm, motorMoreForm);
    ampTag('MotorConfirmA');
  }

  render() {
    const { parsedData, isFWD } = this.state;
    const { setFormState } = this.props;
    if (!parsedData) {
      return null;
    }

    return (
      <Fragment>
        <MotorChoosePlan
          plansData={parsedData}
          isFWD={isFWD}
          setFormState={() => setFormState(FORMSTATES.DETAILFORM.index)}
          othersSetSelection={this.othersSetSelection}
        />
        <RedirectPopup
          ref={node => this.RedirectPop = node}
          onConfirm={this.fwdCheckout}
        />
      </Fragment>
    );
  }
}

MotorChoosePlanContainer.defaultProps = {
};

MotorChoosePlanContainer.propTypes = {
  setSelectedPlanData: PropTypes.func.isRequired,
  setRestrictionPopup: PropTypes.func.isRequired,
  setFormState: PropTypes.func.isRequired,
  loadingEnd: PropTypes.func.isRequired,
  plansData: PropTypes.array.isRequired,
  selectedIndex: PropTypes.number.isRequired,
  motorForm: PropTypes.object.isRequired,
  motorMoreForm: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
  motorForm: getFormValues('motor')(state),
  motorMoreForm: getFormValues('motor-more')(state),
  plansData: state.motor.plansData,
  selectedIndex: state.motor.selectedIndex,
});

const mapDispatchToProps = dispatch => ({
  setFormState: (state) => {
    dispatch(setInsuranceFormState(state));
  },
  loading: () => {
    dispatch(setLoading());
  },
  loadingEnd: () => {
    dispatch(clearLoading());
  },
  setPlanType: (category, type) => {
    dispatch(setPlanType(category, type));
  },
  setSelectedPlanData: (data) => {
    dispatch(setSelectedPlanData(data));
  },
  setRestrictionPopup: (data) => {
    dispatch(setRestrictionPopup(data));
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(MotorChoosePlanContainer);
