/**
 * MotorChoosePlan.js
 * Choose Plan View for Motor
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { Row, Col } from 'reactstrap';
import PropTypes from 'prop-types';

// Import Insurer Logos
import Logos from 'assets/images/InsurerLogos';

import ChangeInitialFormData from '../Templates/ChangeInitialFormData/ChangeInitialFormData';
import CompareView from '../Templates/CompareView/CompareView';
import HeaderDescWithPrice from '../Templates/InsurerFormHeader/HeaderDescWithPrice';
import HeaderDescWithButton from '../Templates/InsurerFormHeader/HeaderDescWithButton';

import MotorEditDetail from './MotorEditDetail';
import Enquiry from '../Templates/Enquiry/Enquiry';

import { setInsuranceFormState, FORMSTATES } from '../../actions/insuranceActions';
import { ampTag } from '../../lib/tagFunctions';

class MotorChoosePlan extends Component {
  constructor(props) {
    super(props);

    this.setSelectedPolicy = this.setSelectedPolicy.bind(this);
    this.doAmpTags = this.doAmpTags.bind(this);
  }

  setSelectedPolicy(clientSlug, clientName, policyName) {
    const { setFormState, othersSetSelection } = this.props;
    othersSetSelection(clientSlug, clientName, policyName);
    setFormState();
  }

  doAmpTags(planTypeIndex = 0) {
    try {
      const { plansData, isFWD } = this.props;

      const data = {
        'plan-name': plansData.selectedInsurers[0].productSlug,
      };

      if (isFWD) {
        const premium = plansData.plans.default.premium[planTypeIndex];
        data.discount = `${((premium.price || 0) - (premium.net_price || 0)) / (premium.price || 0) * 100}%`;
        data.price = premium.net_price || 0;
      }

      ampTag('MotorBuyNowA', data);
    } catch (error) {
      console.log(`Motor choose plan Buy now Amp tag failed: ${error.message}`);
    }
  }

  render() {
    const {
      plansData,
      isFWD,
      setInsurFormState,
      history,
    } = this.props;

    try {
      if (isFWD) {
        for (let i = 0; i < plansData.plans.default.premium.length; ++i) {
          plansData.plans.default.premium[i].doTag = () => this.doAmpTags(i);
        }
      }
    } catch (error) {
      console.log('Overwrite tags failed');
    }

    return (
      <Fragment>
        <Row className="inner-form-container" noGutters>
          <Col className="company-product-container" xs={12}>
            {
              isFWD ?
                <HeaderDescWithPrice
                  logo={Logos[plansData.selectedInsurers[0].insurerSlug]}
                  logoName={`${plansData.selectedInsurers[0].insurerSlug}.svg`}
                  insurerName={plansData.selectedInsurers[0].insurerName}
                  insurerDesc={plansData.policyDetails.about}
                />
              :
                <HeaderDescWithButton
                  logo={Logos[plansData.selectedInsurers[0].insurerSlug]}
                  logoName={`${plansData.selectedInsurers[0].insurerSlug}.svg`}
                  insurerName={plansData.selectedInsurers[0].insurerName}
                  insurerDesc={plansData.policyDetails.about}
                  onClick={() => {
                    this.setSelectedPolicy(plansData.selectedInsurers[0].insurerSlug, plansData.selectedInsurers[0].insurerName, plansData.selectedInsurers[0].productName);
                    this.doAmpTags();
                    setInsurFormState(FORMSTATES.DETAILFORM.index);
                    history.push(`/motor${FORMSTATES.DETAILFORM.path}`);
                  }}
                />
            }
            <Row className="content-container">
              <Col xs={12}>
                <p className="why">{`Why ${plansData.selectedInsurers[0].insurerName}?`}</p>
                <ul>
                  {
                    _.map(plansData.policyDetails.benefits, (benefit, index) => (
                      <li key={`benefit-${index}`}><p>{benefit}</p></li>
                    ))
                  }
                </ul>
              </Col>
              {
                isFWD ?
                  <CompareView
                    category="motor"
                    key="overview"
                    className="overview-container-padding"
                    data={plansData}
                    currentTab="default"
                    editForm={
                      <ChangeInitialFormData
                        form="motor"
                        valuesToListen={[
                          { value: 'brand', desc: 'Brand' },
                          { value: 'model', desc: 'Model' },
                          { value: 'year', desc: 'Year' },
                        ]}
                      >
                        <MotorEditDetail isPopUp ref={node => this.editForm = node} />
                      </ChangeInitialFormData>
                    }
                  />
                :
                  null
              }
            </Row>
          </Col>
          <Enquiry />
        </Row>
      </Fragment>
    );
  }
}

MotorChoosePlan.defaultProps = {
  isFWD: false,
};

MotorChoosePlan.propTypes = {
  plansData: PropTypes.object.isRequired,
  isFWD: PropTypes.bool,
  setFormState: PropTypes.func.isRequired,
  othersSetSelection: PropTypes.func.isRequired,
  setInsurFormState: PropTypes.func.isRequired,
  history: PropTypes.object.isRequired,
};

const mapDispatchToProps = dispatch => ({
  setInsurFormState: (state) => {
    dispatch(setInsuranceFormState(state));
  },
});

export default withRouter(connect(null, mapDispatchToProps)(MotorChoosePlan));
